import React from 'react';
import axios from 'axios';
// import imgNewArrow from '../images/newarrow.svg';
// import imgAnotherArrowOvers from '../images/another_arrow_overs.svg';
import imgProperty from '../images/property.jpg';
import uuidv4 from 'uuid/v4';
// import Select from 'react-select';
// import HeaderMap from '../components/Shared/Header_map';
import numeral from 'numeral';

import Amplify from 'aws-amplify';
import { AmplifyAuthenticator, AmplifySignOut } from '@aws-amplify/ui-react';
import awsconfig from '../aws-exports';
Amplify.configure(awsconfig);

export default class NileListedPropertiesDashboard extends React.Component {
  constructor (props) {
    super(props);

    this.handleShowList = this.handleShowList.bind(this);

    this.state = {
      PropertiesList: undefined
    };
  }

  async componentDidMount () {
    const url = `${process.env.REACT_APP_RIVERDALE_PUBLIC_API}/listedproperties`;
    const requestConfig = {
      headers: {
        'Content-Type': 'application/json; charset=utf-8',
        'X-Api-Key': process.env.REACT_APP_RIVERDALE_PUBLIC_API_KEY
      }
    };

    try {
      const response = await axios.get(url, requestConfig);
      this.setState({ PropertiesList: response.data.Properties, notFound: false, loadingError: false });
    } catch (error) {
      console.log(error);
      var notFound = false;

      if (error.response !== undefined) {
        if (error.response.status === 404) {
          notFound = true;
        }
      }
      this.setState({ propertyDetails: undefined, notFound, loadingError: true });
    }
  }

  componentDidUpdate (prevProps, prevState) {

  }

  handleShowList () {
  }

  render () {
    const properties = this.state.PropertiesList;
    if (!properties) {
      return null;
    }

    console.log(properties);

    return (
      <AmplifyAuthenticator>
        <div className='movedowsn'>
          {
            properties.map(property =>
              <a key={uuidv4()} href={`?propertyid=${property.propertyId}`} target='_blank' rel='noopener noreferrer' className='target_block'>
                <div className='t_img'>
                  <div
                    className='property_image' style={{
                      backgroundImage: `url(${imgProperty})`
                    }}
                  />
                </div>
                <div className='t_infoss'>
                  {property.StreetAddress} <br />
                  {property.City} {property.County} {property.State} {property.Zip9}<br />
                  {numeral(property.ListPrice).format('$0,0')}
                </div>
              </a>
            )
          }
        </div>
        <AmplifySignOut />
      </AmplifyAuthenticator>

    );
  }
}
